import React, { useState, useContext } from 'react';
import { NavLink } from "react-router-dom";

import './navigation.css';

import DataContext from '../data-context';

const MenuItem = ({name, type, img, location}) => {
  return (
    <li class="nav__projects-item">
      <a class="nav__projects-link" href={type === `underDevelopment` ? `/#${img}` : `pages/${location}/`}>{name}</a>
    </li>
  )
};

const Navigation = () => {
  const [opened, setOpened] = useState(false);
  const [navClassName, setNavClassName] = useState('')
  const data = useContext(DataContext);

  const onLinkClick = () => {
    setOpened(false);
    setNavClassName(`nav--closed`);
    window.scrollTo(0, 0);
  };

  const menuItems = data.map((item, i) => <MenuItem key={`${item.name}${i}`} {...item} />);

  const onBurgerClick = () => {
    setOpened((opened) => !opened);
    setNavClassName(opened ? `nav--closed` : `nav--opened`);
  }

  return (
    <nav className={`nav ${navClassName}`}> 
      <button onClick={onBurgerClick} className={`nav__burger ${opened ? `nav__burger--active` : ``}`}>
        <h2 className="visually-hidden">Open menu</h2> 
        <span className="nav__burger-stripe nav__burger-stripe--first"></span>
        <span className="nav__burger-stripe nav__burger-stripe--second"></span> 
        <span className="nav__burger-stripe nav__burger-stripe--third"></span>
      </button>

      <ul className="nav__list">
        <li className="nav__item">
          <p className="nav__item-text">Go back to</p>
          <NavLink activeClassName="nav__link--active" onClick={onLinkClick} to="/" exact={true} className="nav__link nav__link--home">Home</NavLink>
        </li>

        <li className="nav__item">
          <p className="nav__item-text">Projects</p>
          <ul className="nav__projects-list">
            {menuItems}
          </ul>
        </li>

        <li className="nav__item">
          <p className="nav__item-text">Who made this?</p>
          <NavLink activeClassName="nav__link--active" onClick={onLinkClick} to="/about" exact={true} className="nav__link nav__link--about">About</NavLink>
        </li>
      </ul>

    </nav>
  );
}


export default Navigation;