import React from 'react';
import './header.css';

import Letter from '../letter';

const chooseRandomFromArray = array => array[Math.floor(Math.random() * array.length)];
const COLORS = [`#FF8A47`, `#8CEEEE`, `#FC6170`, `#E59DE6`, `#FFD747`, `#4CAF50`];

const Header = () => {
  const TEXT = `Hello, world! I'm Victor, a front-end web developer looking for fun, friends and experience`.split("");

  const title = TEXT.map((letter, i) => {
    const color = chooseRandomFromArray(COLORS);
    return <Letter key={`${letter}${i}`} letter={letter} color={color} />
  });
  
  return (
    <header className="header" id="header">
      <h1 className="header__title">{title}</h1>
    </header>
  );
}


export default Header;