import React from 'react';
import './works.css';

import Project from '../project';

const Works = ({data}) => {
  const projects = data.map((project, i) => <Project key={`${project.name}${i}`} index={i} length={data.length} {...project} />);

  return (
    <section className="works" id="works">
      <h2 className="visually-hidden">My best works</h2>
      <h3 className="works__tag">&lt;projects&gt;</h3>
      {projects}
      <h3 className="works__tag works__tag--close">&lt;/projects&gt;</h3>
    </section>
  );
}


export default Works;