import React, { useContext } from 'react';
import './main.css';

import DataContext from '../data-context';

import Header from '../header';
import Works from '../works';
import WishLuck from '../wish-luck';

const Main = () => {
  const data = useContext(DataContext)

  const works = data.length ? <Works data={data} /> : <p>loading</p>;

  return (
    <React.Fragment>
      <Header />

      {works}

      <WishLuck />
    </React.Fragment>
  );
}


export default Main;